<template>
    <div v-if="ready" class="flex" style='position:relative'>
        <!--left side-->
            <div class="col-7">
                <div class="card">
                    <div class="card-header">
                        <strong>Swap Record</strong>
                    </div>    
                    <div class="card-body" style='padding-left:0;padding-right:0'>
                        <div class="flex">
                            <div class="col-4">
                                <input type="text" class="form-control" v-model="key" placeholder="Enter Search key" @keyup="search_swap"/>
                            </div>
                            <div class="col-8" style='padding-left:0;padding-right:0'>
                                <div class="flex" style='padding-left:0;padding-right:0'>
                                    <div class="flex col-6" style='padding-left:0'>
                                        <div class="col-4" style='padding-top:0.35rem'><label>Start Date</label></div>
                                        <div class="col-8"><input type="date" v-model="st" class="form-control" @change="fetch_swap"/></div>  
                                    </div>
                                    <div class="flex col-6" style='margin-left:auto;padding-right:0'>
                                        <div class="col-4" style='padding-top:0.35rem'><label>End Date</label></div>
                                        <div class="col-8"><input type="date" v-model="ed" class="form-control" @change="fetch_swap"/></div>                              
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-12" style='padding:1rem 0 0 0'>
                            <table class="table table-striped first-td-padding">
                                <thead>
                                    <tr>
                                        <th>Date</th>
                                        <th>Leave</th>
                                        <th>Assigned To</th>
                                        <th>Day</th>
                                        <th>Period</th>
                                        <th>Class</th>
                                        <th>Location</th>
                                        <th>Leave Type</th>
                                        <th>Application Type</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-if="list_leng>0" v-for="i in list_leng">
                                        <td>{{ date[i-1] }}</td>
                                        <td>{{ leave[i-1] }}</td>
                                        <td>{{ assigned[i-1] }}</td>
                                        <td style='text-align:center'>{{ day[i-1] }}</td>
                                        <td>{{ (session[i-1]+1) }}</td>
                                        <!--<td v-if="session[i-1]==0">Class Teacher</td>-->
                                        <td>{{ cls[i-1] }}</td>
                                        <td>{{ location[i-1] }}</td>
                                        <td>{{ type[i-1] }}</td>
                                        <td>{{ app_type[i-1] }}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div> 
        <!--left side-->
        <!--right side-->
        <div class="col-5">
                <div class="card">
                    <div class="card-header">
                        <strong>Counters</strong>
                    </div>    
                    <div class="card-body" style='padding-left:0;padding-right:0;padding-bottom:0'>
                        <div class="flex">
                            <div class="col-3">
                                <button type='button' class="form-control" @click="open_report">Export</button>
                            </div>
                            <div class="col-4">
                                <input type="month" class="form-control" v-model="counter_month" @change="fetch_counter"/>
                            </div>
                            <div class="col-5">
                                <input type="text" class="form-control" v-model="c_key" placeholder="Enter Search key" @keyup="search_counter"/>
                            </div>
                        </div>
                        <div class="col-12" style='padding:1rem 0 0 0'>     <!-- counter table -->
                            <v-data-table
                            :headers="headers"
                            :items="counterlist"
                            class="elevation-1"
                            :sort-by.sync="balance"
                            :sort-desc.sync="isDescending"
                            :rows-per-page-items="[10]"
                            disable-initial-sort
                            >
                            <template v-slot:items="props">
                            <td>{{ props.item.name }}</td>
                            <td>{{ props.item.sub_counter }}</td>
                            <td>{{ props.item.leave_counter }}</td>     
                            <td>{{ props.item.balance  }}</td>                      
                            <template v-slot:pageText="props">
                                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                            </template>
                            
                            </template>
                            </v-data-table>
                        </div>
                    </div>
                </div>
            </div> 
        <!--right side-->
    </div>   
</template>
<script>
export default {
    data(){
        return{
            counter_month:'',
            counterlist:[],
            c_name:[],
            sub:[],
            leave_c:[],
            count_leng:0,
            key:'',
            c_key:'',
            list_leng:0,
            ret_list:[],
            st:'',
            ed:'',
            date:[],
            leave:[],
            day:[],
            session:[],
            location:[],
            cls:[],
            sub:[],
            type:[],
            app_type:[],
            assigned:[],
            ready:false,            
            headers: [
                {text: 'Name',value :'name',sortable: true},
                { text: 'Substitute',value:'sub_counter',sortable: true},
                { text: 'Leave',value:'leave_counter',sortable: true},
                { text: 'Balance',value:'balance',sortable: true},
            ],
        }
    },
    async created(){
        var today = new Date();
        var dd = String(today.getDate()).padStart(2, '0');
        var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        var yyyy = today.getFullYear();
            today = yyyy + '-' + mm + '-' + dd;
        this.counter_month = yyyy+'-'+mm;
        this.st = today;
        this.ed = today;
        this.fetch_swap();
        this.fetch_counter();
        this.ready = true;
    },
    async mounted(){
        
    },
    methods:{
        async search_counter(){
            var formData = new FormData();
            formData.append('month',this.counter_month);
            formData.append('key',this.c_key);
            await axios.post('subsitude/report/backend/counter.php',formData)
                .then(
                    (response)=>{
                        this.count_leng = response.data.length;
                        this.counterlist = response.data;
                        /*
                        var i = 0;
                        for(i=0;i<this.count_leng;i++){
                            this.c_name[i] = response.data[i].name;
                            this.sub[i] = response.data[i].sub_counter;
                            this.leave_c[i] = response.data[i].leave_counter;
                        }*/
                    }
                )
        },
        open_report(){
            window.open("../../src/subsitude/report/backend/export_report.php?month="+this.counter_month);
        },
        async fetch_counter(){
            var formData = new FormData();
            formData.append('month',this.counter_month);
            await axios.post('subsitude/report/backend/counter.php',formData)
                .then(
                    (response)=>{
                        this.count_leng = response.data.length;
                        this.counterlist = response.data;
                        /*
                        var i = 0;
                        for(i=0;i<this.count_leng;i++){
                            this.c_name[i] = response.data[i].name;
                            this.sub[i] = response.data[i].sub_counter;
                            this.leave_c[i] = response.data[i].leave_counter;
                        }*/
                    }
                )
        },
        async fetch_swap(){
            var formData = new FormData();
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            await axios.post('subsitude/report/backend/fetch_swap.php',formData)
                .then(
                    (response)=>{
                        this.ret_list = response.data;
                        this.list_leng = this.ret_list.length;
                        var i = 0;
                        for(i=0;i<this.list_leng;i++){
                            this.leave[i] = this.ret_list[i].leave;
                            this.date[i] = this.ret_list[i].date;
                            this.app_type[i] = this.ret_list[i].app_type;
                            this.assigned[i] = this.ret_list[i].assign;
                            this.cls[i] = this.ret_list[i].class;
                            this.location[i] = this.ret_list[i].location;
                            this.session[i] = this.ret_list[i].session;
                            this.day[i] = this.ret_list[i].day;
                            this.type[i] = this.ret_list[i].type;
                        }
                    }
                )
                this.key='';
        },
        async search_swap(){
            var formData = new FormData();
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            formData.append('key',this.key);
            await axios.post('subsitude/report/backend/fetch_swap.php',formData)
                .then(
                    (response)=>{
                        this.ret_list = response.data;
                        this.list_leng = this.ret_list.length;
                        var i = 0;
                        for(i=0;i<this.list_leng;i++){
                            this.leave[i] = this.ret_list[i].leave;
                            this.date[i] = this.ret_list[i].date;
                            this.app_type[i] = this.ret_list[i].app_type;
                            this.assigned[i] = this.ret_list[i].assign;
                            this.cls[i] = this.ret_list[i].class;
                            this.location[i] = this.ret_list[i].location;
                            this.session[i] = this.ret_list[i].session;
                            this.day[i] = this.ret_list[i].day;
                            this.type[i] = this.ret_list[i].type;
                        }
                    }
                )
        }
    }
}
</script>
<style scoped>
th{
    font-size: 1rem;
}
td{
    font-size:0.95rem;
    transition: all 0.3s ease-in-out;
}

</style>




